import React, { Component } from "react";
import TextField from "@material-ui/core/TextField";
import {
  AppBar,
  Typography,
  InputBase,
  Card,
  CardMedia,
  Avatar,
  IconButton,
  CardHeader,
  CardContent,
  Container,
  Button,
  CardActions,
  CardActionArea,
} from "@material-ui/core";
import "./css/material.css";
import SaveIconOutlined from "@material-ui/icons/SaveOutlined";
import Skeleton from "@material-ui/lab/Skeleton";
import SaveIcon from "@material-ui/icons/Save";

class Material extends Component {
  /*constructor(props) {
    super(props);
    this.ref = React.createRef();
  }*/
  state = {
    ref: "",
  };
  componentDidMount = () => {
    var ref = React.createRef();
    this.setState({ ref });
  };
  intoView = () => {
    this.state.ref.current.scrollIntoView({
      behavior: "smooth",
      block: "nearest",
    });
    /*window.scrollTo(0, this.state.ref.current.offsetTop -80);*/
  };
  render() {
    return (
      <div>
        <AppBar
          style={{
            background: "white",
            height: "50px",
            verticalAlign: "middle",
            display: "flex",
          }}
        >
          <Typography
            style={{
              display: "inline-block",
              color: "black",
              marginLeft: "50px",
              verticalAlign: "middle",
            }}
            noWrap
            variant="h6"
          >
            Intsagram
          </Typography>
          <div className="search">
            <InputBase placeholder="search" />
          </div>
          <div style={{ display: "flex" }}>
            <Button onClick={this.intoView}>TextField</Button>
          </div>
        </AppBar>
        <Card style={{ maxWidth: "300px", margin: "100px auto" }}>
          <CardHeader
            avatar={<Avatar> S</Avatar>}
            title="heading"
            subheader="caption"
            action={
              <IconButton>
                <SaveIconOutlined />
              </IconButton>
            }
          />
          <CardMedia
            style={{ height: "190px" }}
            image="https://pi.tedcdn.com/r/talkstar-photos.s3.amazonaws.com/uploads/72bda89f-9bbf-4685-910a-2f151c4f3a8a/NicolaSturgeon_2019T-embed.jpg?w=512"
          />
          <CardContent>
            <Typography>lorem ipsum is a dummy paragraph</Typography>
          </CardContent>
        </Card>
        <Card style={{ maxWidth: "300px", margin: "100px auto" }}>
          <CardHeader
            avatar={
              <Skeleton
                variant="circle"
                height={40}
                width={40}
                animation="wave"
              />
            }
            title={
              <Skeleton
                variant="rect"
                width="80%"
                animaton="wave"
                height={10}
                style={{ marginBottom: "10px" }}
              />
            }
            subheader={
              <Skeleton
                variant="rect"
                height={10}
                width="70%"
                animaton="wave"
              />
            }
          />
          <Skeleton variant="rect" height={190} width="100%" animation="wave" />
          <CardContent>
            <Skeleton variant="rect" height={30} width="100%" animaton="wave" />
          </CardContent>
        </Card>

        <Container
          ref={this.state.ref}
          style={{
            width: "400px",
            height: "500px",
            border: "solid 1px rgb(0,0,0,0.4)",
          }}
        >
          <TextField
            label="username"
            size="small"
            style={{ height: "20px", marginBottom: "40px", width: "70%" }}
          />
          <br />
          <TextField
            label="password"
            type="password"
            size="small"
            style={{ width: "70%" }}
          />
          <br />
          <Button variant="outlined" color="secondary" size="small">
            SUBMIt
          </Button>
        </Container>
        <Card
          style={{
            maxWidth: "300px",
            margin: "100px auto",
            position: "relative",
          }}
        >
          <CardMedia
            style={{ height: "190px" }}
            image="https://pi.tedcdn.com/r/talkstar-photos.s3.amazonaws.com/uploads/72bda89f-9bbf-4685-910a-2f151c4f3a8a/NicolaSturgeon_2019T-embed.jpg?w=512"
          />
          <CardContent>
            <Typography>
              Lorem ipsumLorem Ipsum is simply dummy text of the printing and
              typesetting industry. Lorem Ipsum has been the industry's standard
              dummy text.
            </Typography>
          </CardContent>

          <CardActions>
            <Avatar>s</Avatar>

            <Typography variant="h7">
              Saiteja Balla
              <Typography vaiant="subtitle1" color="textSecondary">
                {" "}
                web developer
              </Typography>
            </Typography>
            <IconButton
              style={{ position: "absolute", right: "10px", bottom: "10px" }}
            >
              <SaveIcon />
            </IconButton>
          </CardActions>
        </Card>
      </div>
    );
  }
}

export default Material;
